.App {
  text-align: center;
  background-color: rgb(203, 207, 200);
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
.chatItem{
  border: 1px solid gray;
  border-radius: 3px;
  padding: 5px;
  background-color: rgb(234, 236, 213);
  margin: 0 2px 2px 1px;
  width: max-content;
  display: inline-block;
  cursor: pointer;
  box-shadow: grey 3px 4px 5px;
}
.selectedDist{
  background-color: rgb(219, 177, 129);
}
.chatWindow{
  padding: 2px;
  border: 1px solid rgba(71, 53, 5, 0.918);
}
.messageInBubble{
  border: 1px solid rgb(72, 73, 69);
  padding: 5px;
  border-radius: 5px;
  display: grid;
  grid-template-rows: repeat(5, max-content);
  grid-template-columns: repeat(8, 1fr);
  margin-bottom: 5px;
  background-color: rgb(247, 245, 234);
  box-shadow: grey 3px 4px 5px;
}
.centerName{
  font-size: large;
  grid-row: 1/2;
  grid-column: 1/8;
  text-align: left;
  text-shadow: #aca9a9 1px 1px;
}
.centerAddress{
  grid-row: 2/3;
  grid-column: 1/9;
  text-align: left;
}
.centerFee{
  grid-row: 1/2;
  grid-column: 8/9;
}
.sessionContainer{
  grid-column: 1/9;
  display: grid;
  grid-template-columns: repeat(8, 1fr);
  grid-template-rows: repeat(3, max-content);
  border: 1px dashed gray;
  margin-bottom: 5px;
  padding: 5px;
}
.slotContainer{
  overflow: auto;
  grid-row: 3/4;
  grid-column: 1/9;
}
.availableBG{
  background-color: #c2df97;
}
.notAvailableBG{
  background-color: rgb(238, 182, 182);
}
.vaccineDate{
  grid-row: 1/2;
  grid-column: 1/9;
  text-align: left;
  font-weight: 600;
}
.vaccineName{
  grid-row: 1/2;
  grid-column: 1/9;
  text-align: right;
}
.vaccineCapacity{
  grid-row: 2/3;
  grid-column: 1/9;
  text-shadow: #7a7878 1px 1px;
}
.slotTime{
  border: 1px solid green;
  border-radius: 5px;
  background-color: rgb(197, 206, 207);
  padding: 5px;
  margin: 3px;
  display: inline-block;
  width: fit-content;
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
